import React, { useEffect, useState } from 'react';
import styled from 'styled-components';

import {
  PROJECTS,
  TMS,
  BUYERPORTAL,
  FULFILLMENT,
  RETURNMAN,
  SHIPPINGMAN,
  SWEETSPOTFO,
  SWEETSPOTBO,
} from '../../constants';
import Carousel from '../../components/common/Carousel';
import SimpleHeader from '../../components/common/SimpleHeader';
import Footer from '../../components/common/Footer';

const Wrapper = styled.div`
  background: #131313;
  color: #e6e9f0;
  width: 100%;
`;

const Container = styled.div`
  padding: 10% 21%;

  @media (min-width: 1024px) {
    padding: 0 15%;
  }
`;

const Title = styled.div`
  font-size: 1.6rem;
  font-weight: bold;
  color: #87d8d6;
  padding-bottom: 0.5rem;

  &:after {
    content: '';
    display: flex;
    width: 30%;
    border-bottom: 1px solid #87d8d6;
    padding-bottom: 0.5rem;
  }
`;

const ContentWrapper = styled.div`
  padding-bottom: 1.5rem;
`;

const Content = styled.div`
  font-size: 1.3rem;
`;

const Detail = ({ match }) => {
  const [images, setImages] = useState([]);
  const [works, setWorks] = useState([]);

  useEffect(() => {
    switch (match.path) {
      case '/works/tms':
        setImages(TMS);
        setWorks(PROJECTS[0]);
        break;
      case '/works/buyerportal':
        setImages(BUYERPORTAL);
        setWorks(PROJECTS[1]);
        break;
      case '/works/fulfillment':
        setImages(FULFILLMENT);
        setWorks(PROJECTS[2]);
        break;
      case '/works/returnman':
        setImages(RETURNMAN);
        setWorks(PROJECTS[3]);
        break;
      case '/works/shippingman':
        setImages(SHIPPINGMAN);
        setWorks(PROJECTS[4]);
        break;
      case '/works/sweetspotFO':
        setImages(SWEETSPOTFO);
        setWorks(PROJECTS[5]);
        break;
      case '/works/sweetspotBO':
        setImages(SWEETSPOTBO);
        setWorks(PROJECTS[6]);
        break;
      default:
        return;
    }
  }, []);

  return (
    <>
      <Wrapper>
        <SimpleHeader />
        <Carousel images={images} />
        <Container>
          {/* 프로젝트 제목 */}
          <Title>프로젝트</Title>
          <ContentWrapper>
            <Content>{works.title}</Content>
          </ContentWrapper>

          {/* 프로젝트 기간 */}
          <Title>기간</Title>
          <ContentWrapper>
            <Content>{works.period}</Content>
          </ContentWrapper>

          {/* 프로젝트 작업 내용 */}
          <Title>작업 내용</Title>
          <ContentWrapper>
            {works.contents
              ? works.contents.map((item, i) => {
                  if (match.path.includes('buyerportal')) {
                    return (
                      <Content key={i}>
                        -
                        {item.split('@').map((line, i) => {
                          return (
                            <span key={i}>
                              {line}
                              <br />
                            </span>
                          );
                        })}
                      </Content>
                    );
                  } else {
                    return <Content key={i}>-{item}</Content>;
                  }
                })
              : ''}
          </ContentWrapper>

          {/* 프로젝트 사용 기술 */}
          <Title>사용 기술</Title>
          <ContentWrapper>
            {works.spec
              ? works.spec.map((item, i) => {
                  return <Content key={i}>-{item}</Content>;
                })
              : ''}
          </ContentWrapper>
        </Container>
        <Footer />
      </Wrapper>
    </>
  );
};

export default Detail;
