import React, { useEffect, useRef, useState } from 'react';
import styled from 'styled-components';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faCaretSquareLeft,
  faCaretSquareRight,
} from '@fortawesome/free-solid-svg-icons';
import BulletsDiv from './BulletsDiv';

const Wrapper = styled.div`
  width: 100%;
  padding: 20% 0;
  display: flex;
  justify-content: center;
  align-items: center;

  @media (min-width: 1024px) {
    padding: 10% 0;
  }
`;

const SlideContainer = styled.div`
  width: 70%;
  height: 150px;
  position: relative;

  @media (min-width: 1024px) {
    height: 600px;
  }
`;

const Slide = styled.div`
  background: #131313;
  width: 100%;
  height: auto;
  position: absolute;
  display: flex;
  flex-direction: column;

  &.showing {
    z-index: 1;
  }
`;

// github page로 배포하면 background-image 속성을 인식 못하는 것 같음
// const Image = styled.div`
//   background-image: url(${(props) => '../' + props.url});
//   height: 150px;
//   background-size: 100% 100%;
//   background-repeat: no-repeat;
//   display: flex;

//   @media (min-width: 1024px) {
//     height: 600px;
//   }
// `;

// github page로 배포 시에만 해당 const 사용
const Image = styled.img`
  height: 150px;
  display: flex;

  @media (min-width: 1024px) {
    height: 600px;
  }
`;

const ImageDesc = styled.span`
  font-size: 1.2rem;
  text-align: center;
`;

const ArrowButton = styled.div`
  width: 100px;
  height: 100px;
  font-size: 3rem;
  color: #87d8d6;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;

  @media (max-width: 759px) {
    font-size: 1.5rem;
  }
`;

const Carousel = ({ images }) => {
  const [index, setIndex] = useState(0);

  let slideRef = useRef(null);

  useEffect(() => {
    if (slideRef.current) {
      // 페이지 진입 시 0번째 슬라이드를 보여주기 위함
      document.querySelector('#slide0').classList.add('showing');
    }
  });

  const LeftButton = () => {
    if (slideRef.current) {
      let slides = document.getElementsByClassName('slide');
      let prevIndex = index - 1;

      if (prevIndex > -1) {
        setIndex(prevIndex);

        for (let i = 0; i < images.length; i++) {
          slides[i].classList.remove('showing');
        }

        let nextIndex = prevIndex + 1;
        let prevSlideId = `slide${prevIndex}`;
        let nextSlideId = `slide${nextIndex}`;

        let prevSlide = document.querySelector(`#${prevSlideId}`);
        prevSlide.classList.add('showing');

        let nextSlide = document.querySelector(`#${nextSlideId}`);
        nextSlide.classList.remove('showing');
      } else {
        let prevSlide = document.querySelector(`#slide${images.length - 1}`);
        prevSlide.classList.add('showing');
        setIndex(images.length - 1);
      }
    }
  };

  const RightButton = () => {
    if (slideRef.current) {
      let nextIndex = index + 1;

      if (images.length > nextIndex) {
        setIndex(nextIndex);

        let prevIndex = nextIndex - 1;
        let prevSlideId = `slide${prevIndex}`;
        let nextSlideId = `slide${nextIndex}`;

        let prevSlide = document.querySelector(`#${prevSlideId}`);
        prevSlide.classList.remove('showing');

        let nextSlide = document.querySelector(`#${nextSlideId}`);
        nextSlide.classList.add('showing');
      } else {
        let slides = document.getElementsByClassName('slide');
        for (let i = 0; i < images.length; i++) {
          slides[i].classList.remove('showing');
        }
        setIndex(0);
      }
    }
  };

  return (
    <>
      <Wrapper>
        {/* left button */}
        <ArrowButton onClick={LeftButton}>
          <FontAwesomeIcon icon={faCaretSquareLeft} />
        </ArrowButton>

        {/* image slide */}
        <SlideContainer id="slide-container">
          {images
            ? images.map((item, i) => {
                return (
                  <React.Fragment key={i}>
                    <Slide ref={slideRef} id={'slide' + i} className="slide">
                      <Image id={'slide' + i} src={item.src} />
                      <ImageDesc>{item.description}</ImageDesc>
                    </Slide>
                  </React.Fragment>
                );
              })
            : ''}
          <BulletsDiv imgLength={images.length} currentIndex={index} />
        </SlideContainer>

        {/* right button */}
        <ArrowButton onClick={RightButton}>
          <FontAwesomeIcon icon={faCaretSquareRight} />
        </ArrowButton>
      </Wrapper>
    </>
  );
};

export default Carousel;
